import React, { useState, useContext } from 'react';
import {
  Card,
  CardContent,
  Typography,
  Button,
  Box,
  LinearProgress,
  Paper,
  Alert,
  Chip
} from '@mui/material';
import {
  Download,
  Share,
  RestartAlt,
  ArrowBack
} from '@mui/icons-material';
import { AuthContext } from '../services/auth-service';
import { quizQuestions, doshaInfo } from '../data/quizData';

function QuizComponent() {
  const { user } = useContext(AuthContext);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [answers, setAnswers] = useState({});
  const [showResult, setShowResult] = useState(false);
  const [result, setResult] = useState(null);

  const handleAnswerChange = (value) => {
    setAnswers(prev => ({
      ...prev,
      [currentQuestion]: value
    }));
  };

  const handleNext = () => {
    if (currentQuestion < quizQuestions.length - 1) {
      setCurrentQuestion(currentQuestion + 1);
    } else {
      calculateResult();
    }
  };

  const handlePrevious = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(currentQuestion - 1);
    }
  };

  const calculateResult = () => {
    const doshaScores = { vata: 0, pitta: 0, kapha: 0 };
    
    Object.entries(answers).forEach(([questionIndex, answerValue]) => {
      const question = quizQuestions[parseInt(questionIndex)];
      const selectedOption = question.options.find(opt => opt.text === answerValue);
      if (selectedOption) {
        doshaScores[selectedOption.dosha] += selectedOption.score;
      }
    });

    const totalScore = doshaScores.vata + doshaScores.pitta + doshaScores.kapha;
    const percentages = {
      vata: Math.round((doshaScores.vata / totalScore) * 100),
      pitta: Math.round((doshaScores.pitta / totalScore) * 100),
      kapha: Math.round((doshaScores.kapha / totalScore) * 100)
    };

    const dominantDosha = Object.keys(doshaScores).reduce((a, b) => 
      doshaScores[a] > doshaScores[b] ? a : b
    );

    const constitution = getConstitutionType(percentages);

    setResult({
      scores: doshaScores,
      percentages,
      dominantDosha,
      constitution,
      totalAnswers: Object.keys(answers).length,
      date: new Date().toLocaleDateString()
    });
    setShowResult(true);
  };

  const getConstitutionType = (percentages) => {
    const sorted = Object.entries(percentages).sort((a, b) => b[1] - a[1]);
    const highest = sorted[0][1];
    const secondHighest = sorted[1][1];
    
    if (highest >= 60) {
      return `${sorted[0][0].charAt(0).toUpperCase() + sorted[0][0].slice(1)} Dominant`;
    } else if (highest - secondHighest <= 10) {
      return `${sorted[0][0].charAt(0).toUpperCase() + sorted[0][0].slice(1)}-${sorted[1][0].charAt(0).toUpperCase() + sorted[1][0].slice(1)} Dual Constitution`;
    } else {
      return 'Balanced Tridoshic';
    }
  };

  const downloadReport = () => {
    if (!result) return;

    const doshaDetails = doshaInfo[result.dominantDosha];
    const reportContent = `
🕉️ VEDAVERSE PRAKRITI ASSESSMENT REPORT 🕉️
═════════════════════════════════════════════

📊 ASSESSMENT RESULTS
Date: ${result.date}
${user ? `Participant: ${user.displayName || user.email}` : ''}

🧬 DOSHA ANALYSIS
Vata: ${result.percentages.vata}%
Pitta: ${result.percentages.pitta}%
Kapha: ${result.percentages.kapha}%

🎯 CONSTITUTION TYPE
${result.constitution}

🌟 DOMINANT DOSHA: ${doshaDetails.name.toUpperCase()}
Element: ${doshaDetails.element}
Qualities: ${doshaDetails.qualities.join(', ')}

✨ PRIMARY CHARACTERISTICS
${doshaDetails.primaryCharacteristics.map(char => `• ${char}`).join('\n')}

🌿 BALANCING RECOMMENDATIONS
${doshaDetails.balancingTips.map(tip => `• ${tip}`).join('\n')}

🍽️ DIETARY GUIDELINES
${doshaDetails.diet.map(diet => `• ${diet}`).join('\n')}

🧘 LIFESTYLE PRACTICES
${doshaDetails.lifestyle.map(practice => `• ${practice}`).join('\n')}

═════════════════════════════════════════════
Generated by VedaVerse - Divine Ayurvedic Wellness Platform
For more personalized guidance, consult with our certified practitioners.

⚠️ DISCLAIMER: This assessment is for educational purposes only and should not replace professional medical advice.
`;

    const blob = new Blob([reportContent], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `VedaVerse-Prakriti-Report-${new Date().toISOString().split('T')[0]}.txt`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  const shareResult = () => {
    if (navigator.share && result) {
      navigator.share({
        title: 'My VedaVerse Prakriti Assessment',
        text: `I just discovered my Ayurvedic constitution! I'm ${result.constitution} with ${result.percentages[result.dominantDosha]}% ${result.dominantDosha} dominance. 🕉️`,
        url: window.location.href
      });
    } else {
      // Fallback for browsers that don't support Web Share API
      const text = `I just discovered my Ayurvedic constitution on VedaVerse! I'm ${result.constitution} with ${result.percentages[result.dominantDosha]}% ${result.dominantDosha} dominance. 🕉️`;
      navigator.clipboard.writeText(text);
      alert('Result copied to clipboard!');
    }
  };

  const restartQuiz = () => {
    setCurrentQuestion(0);
    setAnswers({});
    setShowResult(false);
    setResult(null);
  };

  if (showResult && result) {
    const doshaDetails = doshaInfo[result.dominantDosha];
    
    return (
      <Box sx={{ maxWidth: 1000, mx: 'auto' }}>
        {/* Header */}
        <Paper 
          sx={{ 
            p: 4, 
            mb: 3, 
            background: 'linear-gradient(135deg, #FFF8E1 0%, #F5DEB3 100%)',
            border: '2px solid #FFD700',
            borderRadius: 3
          }}
        >
          <Typography 
            variant="h3" 
            gutterBottom 
            textAlign="center"
            sx={{
              background: 'linear-gradient(45deg, #8B4513 30%, #FFD700 90%)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              fontWeight: 'bold',
              mb: 2
            }}
          >
            🕉️ Your Divine Prakriti Results 🕉️
          </Typography>
          
          {user && (
            <Alert 
              severity="success" 
              sx={{ 
                mb: 3,
                background: 'linear-gradient(135deg, rgba(76, 175, 80, 0.1) 0%, rgba(139, 195, 74, 0.1) 100%)',
                border: '1px solid #4CAF50'
              }}
            >
              🌟 Results saved to your profile, {user.displayName || user.email}! 🌟
            </Alert>
          )}

          <Box sx={{ textAlign: 'center', mb: 3 }}>
            <Chip 
              label={result.constitution}
              sx={{
                fontSize: '1.2rem',
                py: 3,
                px: 2,
                background: 'linear-gradient(45deg, #8B4513 30%, #A0522D 90%)',
                color: 'white',
                fontWeight: 'bold'
              }}
            />
          </Box>

          {/* Action Buttons */}
          <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center', flexWrap: 'wrap', mb: 4 }}>
            <Button
              variant="contained"
              startIcon={<Download />}
              onClick={downloadReport}
              sx={{ minWidth: 180 }}
            >
              Download Report
            </Button>
            <Button
              variant="outlined"
              startIcon={<Share />}
              onClick={shareResult}
              sx={{ minWidth: 150 }}
            >
              Share Result
            </Button>
            <Button
              variant="outlined"
              startIcon={<RestartAlt />}
              onClick={restartQuiz}
              sx={{ minWidth: 150 }}
            >
              Retake Quiz
            </Button>
          </Box>
        </Paper>

        {/* Dosha Distribution */}
        <Paper sx={{ p: 4, mb: 3 }}>
          <Typography variant="h5" gutterBottom color="primary" textAlign="center">
            🧬 Dosha Distribution Analysis
          </Typography>
          
          {Object.entries(result.percentages).map(([dosha, percentage]) => (
            <Box key={dosha} sx={{ mb: 3 }}>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                <Typography 
                  variant="h6" 
                  sx={{ 
                    textTransform: 'capitalize', 
                    fontWeight: 600,
                    color: dosha === result.dominantDosha ? '#8B4513' : '#5D4037'
                  }}
                >
                  {dosha.charAt(0).toUpperCase() + dosha.slice(1)} ({doshaInfo[dosha].element})
                </Typography>
                <Typography variant="h6" sx={{ fontWeight: 600 }}>
                  {percentage}%
                </Typography>
              </Box>
              <LinearProgress
                variant="determinate"
                value={percentage}
                sx={{ 
                  height: 12, 
                  borderRadius: 6,
                  backgroundColor: 'rgba(139, 69, 19, 0.1)',
                  '& .MuiLinearProgress-bar': {
                    background: dosha === result.dominantDosha 
                      ? 'linear-gradient(45deg, #FFD700 30%, #DAA520 90%)'
                      : 'linear-gradient(45deg, #8B4513 30%, #A0522D 90%)',
                    borderRadius: 6
                  }
                }}
              />
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                Qualities: {doshaInfo[dosha].qualities.join(' • ')}
              </Typography>
            </Box>
          ))}
        </Paper>

        {/* Detailed Analysis */}
        <Paper sx={{ p: 4, mb: 3 }}>
          <Typography variant="h5" gutterBottom color="primary" textAlign="center">
            🌟 Your {doshaDetails.name} Constitution
          </Typography>
          
          <Typography variant="body1" paragraph sx={{ fontSize: '1.1rem', lineHeight: 1.7, textAlign: 'center', mb: 4 }}>
            You have a predominantly {doshaDetails.name} constitution, governed by the {doshaDetails.element} elements. 
            This sacred combination shapes your unique physical, mental, and emotional characteristics.
          </Typography>
          
          <Box sx={{ display: 'grid', gap: 3, gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' } }}>
            <Card sx={{ p: 3 }}>
              <Typography variant="h6" gutterBottom color="primary">
                ✨ Key Characteristics
              </Typography>
              <ul style={{ margin: 0, paddingLeft: '20px' }}>
                {doshaDetails.primaryCharacteristics.map((char, index) => (
                  <li key={index} style={{ marginBottom: '8px' }}>
                    <Typography variant="body2">{char}</Typography>
                  </li>
                ))}
              </ul>
            </Card>

            <Card sx={{ p: 3 }}>
              <Typography variant="h6" gutterBottom color="primary">
                🌿 Balancing Practices
              </Typography>
              <ul style={{ margin: 0, paddingLeft: '20px' }}>
                {doshaDetails.balancingTips.map((tip, index) => (
                  <li key={index} style={{ marginBottom: '8px' }}>
                    <Typography variant="body2">{tip}</Typography>
                  </li>
                ))}
              </ul>
            </Card>

            <Card sx={{ p: 3 }}>
              <Typography variant="h6" gutterBottom color="primary">
                🍽️ Dietary Guidelines
              </Typography>
              <ul style={{ margin: 0, paddingLeft: '20px' }}>
                {doshaDetails.diet.map((dietary, index) => (
                  <li key={index} style={{ marginBottom: '8px' }}>
                    <Typography variant="body2">{dietary}</Typography>
                  </li>
                ))}
              </ul>
            </Card>

            <Card sx={{ p: 3 }}>
              <Typography variant="h6" gutterBottom color="primary">
                🧘 Lifestyle Practices
              </Typography>
              <ul style={{ margin: 0, paddingLeft: '20px' }}>
                {doshaDetails.lifestyle.map((practice, index) => (
                  <li key={index} style={{ marginBottom: '8px' }}>
                    <Typography variant="body2">{practice}</Typography>
                  </li>
                ))}
              </ul>
            </Card>
          </Box>
        </Paper>

        {/* Next Steps */}
        <Paper sx={{ p: 4, textAlign: 'center' }}>
          <Typography variant="h6" gutterBottom color="primary">
            🚀 Continue Your Wellness Journey
          </Typography>
          <Typography variant="body1" paragraph>
            Now that you know your constitution, explore personalized herbs and yoga practices, 
            or consult with our certified Ayurvedic practitioners for deeper guidance.
          </Typography>
          <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center', flexWrap: 'wrap', mt: 3 }}>
            <Button variant="contained" onClick={() => window.location.href = '#herbs'}>
              Explore Herbs 🌿
            </Button>
            <Button variant="contained" onClick={() => window.location.href = '#yoga'}>
              Practice Yoga 🧘‍♀️
            </Button>
            <Button variant="outlined" onClick={() => window.location.href = '#doctors'}>
              Consult Practitioners 👨‍⚕️
            </Button>
          </Box>
        </Paper>
      </Box>
    );
  }

  const progress = ((currentQuestion + 1) / quizQuestions.length) * 100;
  const currentQ = quizQuestions[currentQuestion];

  return (
    <Box sx={{ maxWidth: 900, mx: 'auto' }}>
      {/* Header */}
      <Paper 
        sx={{ 
          p: 4, 
          mb: 3, 
          background: 'linear-gradient(135deg, #FFF8E1 0%, #F5DEB3 100%)',
          border: '2px solid #FFD700',
          borderRadius: 3,
          textAlign: 'center'
        }}
      >
        <Typography 
          variant="h3" 
          gutterBottom
          sx={{
            background: 'linear-gradient(45deg, #8B4513 30%, #FFD700 90%)',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            fontWeight: 'bold',
            mb: 2
          }}
        >
          🕉️ Divine Prakriti Assessment 🕉️
        </Typography>
        <Typography variant="h6" color="text.secondary" paragraph>
          Discover your authentic Ayurvedic constitution through this sacred 20-question journey
        </Typography>
        
        {/* Progress */}
        <Box sx={{ mt: 3 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
            <Typography variant="body2" color="text.secondary">
              Question {currentQuestion + 1} of {quizQuestions.length}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {Math.round(progress)}% Complete
            </Typography>
          </Box>
          <LinearProgress
            variant="determinate"
            value={progress}
            sx={{ 
              height: 12, 
              borderRadius: 6,
              backgroundColor: 'rgba(139, 69, 19, 0.1)',
              '& .MuiLinearProgress-bar': {
                background: 'linear-gradient(45deg, #FFD700 30%, #DAA520 90%)',
                borderRadius: 6
              }
            }}
          />
        </Box>
      </Paper>

      {/* Question Card */}
      <Paper 
        sx={{ 
          p: 4,
          background: 'linear-gradient(135deg, rgba(255, 248, 225, 0.9) 0%, rgba(245, 222, 179, 0.9) 100%)',
          backdropFilter: 'blur(10px)',
          border: '1px solid rgba(255, 215, 0, 0.3)'
        }}
      >
        <Typography 
          variant="h5" 
          gutterBottom 
          textAlign="center"
          sx={{ 
            color: '#8B4513', 
            fontWeight: 600,
            mb: 4
          }}
        >
          {currentQ.question}
        </Typography>

        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          {currentQ.options.map((option, index) => (
            <Card
              key={index}
              sx={{
                cursor: 'pointer',
                transition: 'all 0.3s ease',
                border: '2px solid transparent',
                background: 'linear-gradient(135deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 248, 225, 0.8) 100%)',
                '&:hover': {
                  transform: 'translateY(-2px)',
                  boxShadow: '0 8px 25px rgba(139, 69, 19, 0.15)',
                  border: '2px solid #FFD700'
                }
              }}
              onClick={() => handleAnswer(option.dosha)}
            >
              <CardContent sx={{ p: 3 }}>
                <Typography 
                  variant="body1" 
                  sx={{ 
                    fontSize: '1.1rem',
                    lineHeight: 1.6,
                    color: '#5D4037'
                  }}
                >
                  {option.text}
                </Typography>
              </CardContent>
            </Card>
          ))}
        </Box>

        {/* Navigation */}
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 4 }}>
          <Button
            variant="outlined"
            onClick={handlePrevious}
            disabled={currentQuestion === 0}
            startIcon={<ArrowBack />}
            sx={{ minWidth: 120 }}
          >
            Previous
          </Button>
          
          <Box sx={{ display: 'flex', gap: 2 }}>
            <Button
              variant="outlined"
              onClick={restartQuiz}
              startIcon={<RestartAlt />}
            >
              Restart
            </Button>
            
            {currentQuestion === quizQuestions.length - 1 && (
              <Button
                variant="contained"
                onClick={calculateResult}
                size="large"
                sx={{ 
                  minWidth: 150,
                  background: 'linear-gradient(45deg, #8B4513 30%, #A0522D 90%)',
                  '&:hover': {
                    background: 'linear-gradient(45deg, #A0522D 30%, #8B4513 90%)'
                  }
                }}
              >
                See Results ✨
              </Button>
            )}
          </Box>
        </Box>
      </Paper>

      {/* Sacred Knowledge Box */}
      <Paper 
        sx={{ 
          p: 3, 
          mt: 3, 
          background: 'linear-gradient(135deg, rgba(139, 69, 19, 0.05) 0%, rgba(160, 82, 45, 0.05) 100%)',
          border: '1px solid rgba(139, 69, 19, 0.2)'
        }}
      >
        <Typography variant="body2" textAlign="center" color="text.secondary">
          🌟 Each answer reveals the cosmic energies (Vata, Pitta, Kapha) that shape your being. 
          Answer honestly to receive your most authentic constitutional reading. 🌟
        </Typography>
      </Paper>
    </Box>
  );
};

export default QuizComponent;